import * as layout from "../../routes/$layout.js";

const components = [
	() => import("../../routes/index.js"),
	() => import("../../routes/readme.js"),
	() => import("../../routes/about/index.js"),
	() => import("../../routes/about/go.js"),
	() => import("../../routes/blog/[id([0-9]+)].js")
];

const d = decodeURIComponent;
const empty = () => ({});

export const pages = [
	{
		// index.svelte
		pattern: /^\/$/,
		params: empty,
		parts: [components[0]]
	},

	{
		// readme.svelte
		pattern: /^\/readme\/?$/,
		params: empty,
		parts: [components[1]]
	},

	{
		// about/index.svelte
		pattern: /^\/about\/?$/,
		params: empty,
		parts: [components[2]]
	},

	{
		// about/go.svelte
		pattern: /^\/about\/go\/?$/,
		params: empty,
		parts: [components[3]]
	},

	{
		// blog/[id([0-9]+)].svelte
		pattern: /^\/blog\/([0-9]+)\/?$/,
		params: (m) => ({ id: d(m[1])}),
		parts: [components[4]]
	}
];

export const ignore = [
	
];

export { layout };